body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.search {
  width: 240px;
  margin: auto auto 20px ;
}
.flash-card{
  box-shadow: 0 0.3125rem 1.25rem 0 rgb(0 0 0 / 16%);
  padding: 10px 20px;
  width: 240px;
  margin: auto;
  border-radius: 6px;
}
#en {
  text-align: center;
  font-weight: 500;
}
#ipa{
  font-weight: 500;
}
@media only screen and (max-width: 600px) {
  .search {
    width: 90%;
  }
  .flash-card{
    width: 90%;
    width: calc(90% - 40px);
    margin: 10px 20px;
  }
}
.wl-input {
  box-sizing: border-box;
  margin: 0;
  font-variant: tabular-nums;
  list-style: none;
  font-feature-settings: "tnum";
  position: relative;
  display: inline-block;
  width: 100%;
  min-width: 0;
  padding: 4px 11px;
  color: #000000d9;
  font-size: 14px;
  line-height: 1.5715;
  background-color: #fff;
  background-image: none;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
  transition: all .3s;
}


.select {
  position: relative;
  display: inline-block;
  width: 100%;
}

.select .select-icon {
  display: block;
  width: 1em;
  height: 1em;
  fill: currentColor;
}

.select:focus {
  outline: 0;
}

.select:focus .selection {
  box-shadow: 0 0 1px 1px #00a9e0;
}

.label {
  display: block;
  margin-bottom: 6px;
  font-weight: 600;
}

.selection,
.test {
  position: relative;
  padding: 5px;
  border: 1px solid #ddd;
  background: #ffffff;
  height: 26px;
}

@media screen and (max-width: 1024px) {
  .selection,
  .test {
    width: calc(100% - 10px);
  }
}

.value {
  position: relative;
  display: inline-block;
  padding: 5px 10px;
}

.multiple {
  padding-right: 30px;
  margin-right: 5px;
  background: #d9f2fb;
  color: #00a9e0;
}

.delete {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  padding: 10px;
  font-size: 10px;
  cursor: pointer;
}

.placeholder {
  padding: 1px 10px;
  color: #898989;
}

.arrow {
  position: absolute;
  top: 5px;
  right: 5px;
  display: block;
  padding: 10px;
  font-size: 10px;
  color: #898989;
}

@media screen and (max-width: 1024px) {
  .arrow {
    right: 15px;
  }
}

.options {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  border: solid #ddd;
  border-width: 0 1px;
  background: #ffffff;
  z-index: 33333;
}

@media screen and (max-width: 1024px) {
  .options {
    width: 100%;
  }
}

.option {
  padding: 10px 15px;
  border-bottom: 1px solid #ddd;
  cursor: pointer;
}

.option.selected {
  border: 1px solid #00a9e0;
  margin: -1px -1px 0;
  background: #d9f2fb;
}

.option.focused {
  background: #f5f5f5;
}

.checkbox {
  content: '';
  vertical-align: top;
  display: inline-block;
  width: 16px;
  height: 16px;
  padding: 2px;
  border: 1px solid #ddd;
  border-radius: 2px;
  margin: 2px 12px 0 0;
  color: #fff;
  font-size: 10px;
}

.selected .checkbox {
  border-color: #007da6;
  background: #00a9e0;
}
.wl-btn {
  line-height: 1.5715;
  position: relative;
  display: inline-block;
  font-weight: 400;
  white-space: nowrap;
  text-align: center;
  background-image: none;
  border: 1px solid transparent;
  box-shadow: 0 2px #00000004;
  cursor: pointer;
  transition: all .3s cubic-bezier(.645,.045,.355,1);
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  touch-action: manipulation;
  height: 32px;
  padding: 4px 15px;
  font-size: 14px;
  border-radius: 2px;
  color: #000000d9;
  border-color: #d9d9d9;
  background: #fff;
}
.wl-btn-primary {
  color: #fff;
  border-color: #1890ff;
  background: #1890ff;
  text-shadow: 0 -1px 0 rgb(0 0 0 / 12%);
  box-shadow: 0 2px #0000000b;
}
.btn-filter{
  display: block;
  margin: 20px auto;
}
.voulume{
  height: 24px;
  width: 24px;
}
.group{
  display: flex;
  align-content: center;
  justify-content: space-between;
}